import React from "react";
import Footer from '../../components/Footer';
import Header from "../../components/Header";
import './../../App.css';
import Web3 from 'web3'
import strings from "../../components/Language";
import {CONTRACT_ADDRESS,ABI,NODE_URL} from './../../config/mistrybox';
import {CETB_ADDRESS,CETB_ABI} from './../../config/cetb';
import api from './../../config/api';
import { toast } from 'react-toastify';
import axios from 'axios';
import {alertMessage} from '../../components/Util';
class Wheel extends React.Component {
    state = {
        list: [
          strings.prize_1,
          strings.prize_2,
          strings.prize_3,
          strings.prize_4,
          strings.prize_5,
          strings.prize_6,
          strings.prize_7,
          strings.prize_8,
          strings.prize_9,
        ],
        rotate: 0,
        easeOut: 0,
        angle: 0,
        top: null,
        offset: null,
        net: null,
        result: null,
        spinning: false,
        predefine_result:0,

        Contract:null,
        account:null,
        is_connected:false,
        is_approve_cetb:0,
        onpress:0,
        bet_list:[],
        spin_balance:0,
        package_id:1,
        auth_token:null,
        balance_start:false,
        lang:strings.getLanguage(),
    }

    constructor() {
        super();
        this.prizePopup = React.createRef();
        this.signPopup = React.createRef();
    }

    async componentDidMount() {
        let that=this;
        this.renderWheel();
        this.setState({auth_token:localStorage.getItem('auth_token')});
        if(window.ethereum){
            const web3 = new Web3(window.ethereum || NODE_URL);
            const TokenContract = new web3.eth.Contract(CETB_ABI, CETB_ADDRESS);
            let accounts=await web3.eth.getAccounts();
            if(accounts.length){
				this.setState({is_connected:true})
				this.setState({account:accounts[0]}) 
                const user= await TokenContract.methods.allowance(accounts[0],CONTRACT_ADDRESS).call();
                if(user>0){
                    this.setState({is_approve_cetb:1})
                }
                this.getBalance();
                if(this.state.auth_token){
                    
                }
                else{
                    this.signPopup.current.click();
                }
            }
        }
        else{
        }
    }
    
    renderWheel(){
        let numOptions = this.state.list.length;
        let arcSize = (2 * Math.PI) / numOptions;
        this.setState({
          angle: arcSize
        });
        this.topPosition(numOptions, arcSize);
    }
    
    topPosition = (num, angle) => {
        let topSpot = null;
        let degreesOff = null;
        if (num === 9) {
          topSpot = 7;
          degreesOff = Math.PI / 2 - angle * 2;
        } else if (num === 8) {
          topSpot = 6;
          degreesOff = 0;
        } else if (num <= 7 && num > 4) {
          topSpot = num - 1;
          degreesOff = Math.PI / 2 - angle;
        } else if (num === 4) {
          topSpot = num - 1;
          degreesOff = 0;
        } else if (num <= 3) {
          topSpot = num;
          degreesOff = Math.PI / 2;
        }
        this.setState({
          top: topSpot,
          offset: degreesOff
        });
    };
    
    spin = () => {
        let rnd=Math.floor(this.state.predefine_result);
        let randomSpin = 3600-(rnd*40-40)+0.25;
        console.log(rnd)
        this.setState({
          rotate: randomSpin,
          easeOut: 5,
          spinning: true
        });
    
        setTimeout(() => {
          this.getResult(randomSpin);
          this.decodeResult();
        }, 5000);
    };
    
    getResult = spin => {
        const { angle, top, offset, list } = this.state;
        let netRotation = ((spin % 360) * Math.PI) / 180;
        let travel = netRotation + offset;
        let count = top;
        while (travel > 0) {
          travel = travel - angle;
          count--;
        }
        let result;
        if (count >= 0) {
          result = count;
        } else {
          result = list.length + count;
        }
    
        this.setState({
          net: netRotation,
          result: result+1
        });
    };
    
    reset = () => {
        this.setState({
          rotate: 0,
          easeOut: 0,
          result: null,
          spinning: false,
          randomSpin:0,
        });
    };

    async approveCETB(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const Token = new web3.eth.Contract(CETB_ABI, CETB_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                Token.methods.approve(CONTRACT_ADDRESS,1000000*10 ** 9).send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    that.setState({onpress:0})
                    that.setState({is_approve_cetb:1})
                    resolve(true)
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.approved,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    /**
     * Buy Spin
     */
    async doBuy(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const Contract = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                Contract.methods.buy(this.state.package_id).send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    try{
                        that.fetchBalance();
                    }
                    catch(e){
                        console.log(e)
                    }
                    that.setState({onpress:0})
                    resolve(true);
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.purchased,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    async fetchBalance(){
        let that=this;
        await axios({
            method: 'post', url: api.spin_wheel_fetch,
            data: {
                wallet:this.state.account
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){
           //that.setState({spin_balance:ress.data.balance})
        }).catch(function (e) {
            console.log(e)
        });
    }
    
    /**
     * Get Balance
     */
     async getBalance(){
        let that=this;
        await axios({
            method: 'post', url: api.spin_wheel_balance,
            data: {
                wallet:this.state.account
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){  
            if(ress.data.error===200){
                that.setState({spin_balance:ress.data.spin})
                if(that.state.balance_start==false){
                    that.setState({balance_start:true})
                    setInterval(function () {that.getBalance();}, 10000);
                }
           }
           else{
                that.signPopup.current.click();
           }
        }).catch(function (e) {
            if(e.response.status===401){
                that.signPopup.current.click();
            }
        });
    }

    /**
     * Play 
     */
    async doPlay(){
        if(this.state.is_connected){
            let that=this;
            await axios({
                method: 'post', url: api.spin_wheel_bet,
                data: {
                    wallet:this.state.account
                },
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Accept-Language' : this.state.lang,
                    'Authorization': 'Bearer ' + this.state.auth_token,
                }
            }).then(function (ress){
            if(ress.data.error===200){
                that.setState({predefine_result:ress.data.result})
                that.spin();
                that.getBalance();
            }
            else{
                alertMessage(ress.data.message);
            }
            }).catch(function (e) {
                if(e.response.status===401){
                    that.signPopup.current.click();
                }
            });
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    decodeResult(){
        this.prizePopup.current.click();
    }

    getResultName(res){
        return strings['prize_'+res];
    }

    /**
     * Get Play Bet List
     * @param {string} wallet 
     */
    async getBetList(wallet){
        let that=this;
        await axios({
            method: 'get', url: api.spin_wheel_list+"?wallet="+this.state.account,
            data: {
                wallet: wallet
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){
           that.setState({bet_list:ress.data.bets.data});
        }).catch(function (e) {
            if(e.response.status===401){
                that.signPopup.current.click();
            }
        });
    }

    async doSign(){
        let that=this;
        if(this.state.is_connected){
            const web3 = new Web3(window.ethereum);
            const string = "THE_CETA_PROJECT";
            const accounts = await web3.eth.getAccounts();
            const signature = new Promise((resolve, reject) =>{
                web3.eth.personal.sign(string, accounts[0], "test").then(function(data){
                    console.log(data);
                    that.login(accounts[0],data);
                    resolve(true);
                });
            });
            console.log(signature)
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    /**
     * Get Balance
     */
     async login(wallet,secret){
        await axios({
            method: 'post', url: api.register,
            data: {
                wallet:wallet,
                secret:secret,
                device_name:"CETA"
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept-Language' : this.state.lang,
                'Accept': 'application/json',
            }
        }).then(function (ress){
            localStorage.setItem("auth_token",ress.data);
            setTimeout(function(){
                window.location.reload();
            },2000)
        }).catch(function (e) {
            console.log(e)
        });
    }

    render() {
        let self=this;
        return (
            <div>
                <div id="main-wrapper">
                    <Header/>
                    <div className="content-body">
                        <div className="container-fluid">
                            <div className="row mb-4 text-center align-content-center ">
                                <div className="col-lg-6 mx-auto">
                                    <img src="images/ce-lucky-box.png" className="img-fluid" alt="CE Luckybox" onClick={()=>this.doSign()}/>
                                </div>
                            </div>	
                            <div className="row mb-4">
                                <div className="col-lg-6 mx-auto">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="col-12 mx-auto text-center">
                                                <h4>{strings.spin_balance} : <span className="badge badge-primary">{this.state.spin_balance}</span></h4>
                                                <button className="btn btn-primary btn-sm mt-2" data-toggle="modal" data-target="#buy_spin_modal">
                                                    {strings.buy_spin}
                                                </button>
                                            </div>
                                            <div className="col-12 mx-auto text-center">
                                                <span className="selector">&#9660;</span>
                                                <div
                                                    id="wheel"
                                                    width="360"
                                                    height="360"
                                                    style={{
                                                        WebkitTransform: `rotate(${this.state.rotate}deg)`,
                                                        WebkitTransition: `-webkit-transform ${this.state.easeOut}s ease-out`,
                                                        marginTop:-20,
                                                    }}
                                                >
                                                    <img src="images/spinwheel/wheel.svg" className="head-mobile-img" alt="CE Luckybox"/>
                                                </div>
                                                <div className="col-12 mx-auto text-center">
                                                    {
                                                        this.state.spinning ? (
                                                            <button className="btn btn-danger btn-sm mt-2" type="button" id="reset" onClick={this.reset}>
                                                                {strings.reset}
                                                            </button>
                                                            ) 
                                                            : 
                                                            (
                                                            <button className="btn btn-success btn-sm mt-2"  type="button" id="spin" onClick={()=>this.doPlay()}>
                                                                {strings.spin}
                                                            </button>
                                                        )
                                                    }

                                                </div>
                                            </div>
                                            <div className="col-12 mt-2 mx-auto text-center">
                                                <a rel="noreferrer" href="#" data-toggle="modal" data-target="#claim_modal" onClick={()=>this.getBetList()}><small className="badge badge-primary">{strings.view_history}</small></a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>	
                            
                        </div>
                    </div>
                    <button ref={this.prizePopup} type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#prize_modal">Small modal</button>
                    <button ref={this.signPopup} type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#sign_modal">Small modal</button>
                    <div className="modal fade" id="prize_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <div className="col-12 text-center">
                                        <h4 className="text-center modal-title">{strings.you_win}</h4>
                                    </div>
                                    <button type="button" className="close" data-dismiss="modal"><span>&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <div className="col-12 luckyboxItem text-center mx-auto">
                                        <img src={"images/spinwheel/prize_"+this.state.predefine_result+".svg"} className="head-mobile-img"/>
                                    </div>
                                    <h5 className="text-center">{this.getResultName(this.state.predefine_result)}</h5>
                                </div>
                                <div className="modal-footer mx-auto">
                                    <button type="button" className="btn btn-danger light" data-dismiss="modal">{strings.close}</button>
                                </div>
                            </div>
                        </div>
                    </div>	

                    <div className="modal fade" id="claim_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <div className="col-12 text-center">
                                        <h4 className="text-center modal-title">{strings.claim_history}</h4>
                                    </div>
                                    <button type="button" className="close" data-dismiss="modal"><span>&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <div className="col-12 luckyboxItem text-center mx-auto">
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <th scope="col">{strings.round}</th>
                                                    <th scope="col">{strings.prize}</th>
                                                    <th scope="col">{strings.status}</th>
                                                    <th scope="col">{strings.date}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {

                                                    this.state.bet_list.map(function(data,index){
                                                        return(
                                                            <tr key={index}>
                                                                <th scope="row" className="text-white">{data.round}</th>
                                                                <td className="text-white">{self.getResultName(data.result)}</td>
                                                                <td className="text-white">{data.status?strings.collected:strings.pending}</td>
                                                                <td className="text-white">{data.created_at}</td>
                                                            </tr>
                                                        );
                                                    })
                                                }
                                                
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div className="modal-footer mx-auto">
                                    <button type="button" className="btn btn-danger light" data-dismiss="modal">{strings.close}</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal fade" id="buy_spin_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="staticBackdropLabel">{strings.buy_spin}</h5>
                                </div>
                                <div className="modal-body">
                                    <div className="form-group">
                                        <label htmlFor="recipient-name" className="col-form-label">{strings.choose_set}:</label>
                                        <select className="form-control" onChange={(e)=>this.setState({package_id:e.target.value})}>
                                            <option value="1">{strings.set}-1</option>
                                            <option value="2">{strings.set}-2</option>
                                            <option value="3">{strings.set}-3</option>
                                            <option value="4">{strings.set}-4</option>
                                            <option value="5">{strings.set}-5</option>
                                            <option value="6">{strings.set}-6</option>
                                            <option value="7">{strings.set}-7</option>
                                            <option value="8">{strings.set}-8</option>
                                            <option value="9">{strings.set}-9</option>
                                            <option value="10">{strings.set}-10</option>
                                        </select>
                                    </div>
                                    <div className="form-group text-center">
                                        {
                                            this.state.is_approve_cetb==0?
                                                this.state.onpress?
                                                    <button type="button" className="my-4 btn btn-success btn-rounded">
                                                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                        <span className="visually-hidden">&nbsp;{strings.loading}</span>
                                                    </button>
                                                :
                                                    <button type="button" className="my-4 btn btn-success btn-rounded" onClick={()=>this.approveCETB()}>{strings.approve} CETB</button>
                                            :null
                                        }
                                        {
                                            this.state.onpress && this.state.is_approve_cetb?
                                                <button type="button" className="btn btn-primary">
                                                    <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                    <span className="visually-hidden">&nbsp;{strings.loading}</span>
                                                </button>
                                            :
                                                this.state.is_approve_cetb?
                                                    <button type="button" className="btn btn-primary" onClick={()=>this.doBuy()}>{strings.confirm}</button>
                                                :
                                                    null
                                        }
                                    </div>
                                </div>
                                <div className="modal-footer justify-content-center">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">{strings.close}</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal fade" id="sign_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="staticBackdropLabel">{strings.signin_to_continue}</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body text-center">
                                <h6>{this.state.account}</h6>
                                    <button type="button" className="btn btn-success" onClick={()=>this.doSign()}>{strings.signin}</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Footer/>
                </div>
            </div>
        );
    }
}
export default Wheel;