    async componentDidMount(){
        // this.setState({auth_token:localStorage.getItem('auth_token')});
        if(window.ethereum){
            const web3 = new Web3(window.ethereum || NODE_URL);
            const Staking = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const CETCStaking = new web3.eth.Contract(CETCStaking_ABI, CETCStaking_ADDRESS);
            const node = await Staking.methods.stakePool().call()
            let accounts=await web3.eth.getAccounts();
            if(accounts.length){
                this.setState({account:accounts[0]})
                // this.getBalance();

                //Check For Approve
                const TokenContract = new web3.eth.Contract(CETA_ABI, CETA_ADDRESS);
                const is_approve= await TokenContract.methods.allowance(accounts[0],CONTRACT_ADDRESS).call();
                if(is_approve>=300000000000){
                    this.setState({is_approve_ceta:1})
                }

                // if(!this.state.auth_token){
                //     this.signPopup.current.click();
                // }

                const user = await Staking.methods._user(accounts[0]).call();
                const userCETCStaking = await CETCStaking.methods._user(accounts[0]).call();
                // this.getV1List();
                const stake_list = await Staking.methods.stackelist(accounts[0]).call()
                this.setState({_my_stake:stake_list});
                let ceta=0;
                let usdt=0;
                stake_list.forEach(function(element){
                    if(element.expired_at<=Math.floor(new Date().getTime()/1000.0)==1){
                    }
                    else{
                        ceta+=parseFloat(element.ceta);
                        usdt+=parseFloat(element.other);
                    }
                });
                this.setState({ceta,usdt});
                this.setState({ceta_balance:user[0],cetb_balance:user[1]});
                this.setState({cetc_balance:userCETCStaking[0]});
                this.setState({is_connected:true}); 

            }
            
            if(node){
                let total=(parseInt(node)+parseInt(1004100));
                this.setState({usdt_pool:total});
            }
        }
        else{
            const web3 = new Web3(NODE_URL);
            const Staking = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const node = await Staking.methods.liquidityPool().call()
            if(node){
                let total=(parseInt(node)+parseInt(1004100));
                this.setState({usdt_pool:total});
            }
        }
    }

