    /**
     * Do Centralized Registration
     * @returns 
     */
    async doRegister(){
        if(this.state.is_connected){
            let that=this;
            this.setState({onpress:1})
            let sponsor=this.state.sponsor;
            if (!/^0x[a-fA-F0-9]{40}$/.test(sponsor)){
                alertMessage(strings.invalid_sponsor_address);
                this.setState({onpress:0})
                return false;
            }
            else {
                await axios({
                    method: 'post', url: api.register_stake_user,
                    data: {
                        sponsor: sponsor,
                    },
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Accept-Language' : this.state.lang,
                        'Authorization': 'Bearer ' + this.state.auth_token,
                    }
                }).then(function (ress){
                   console.log(ress)
                   if(ress.data.error==200){
                        successMessage(strings.register_success);
                        setTimeout(function(){
                            window.location.reload();
                        },3000)
                   }
                   else if(ress.data.error==202){
                        alertMessage(ress.data.message);
                   }
                }).catch(function (e) {

                });
            }
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

