import React from "react";
import Footer from '../components/Footer';
import Header from "../components/Header";
import strings from "../components/Language";
import './../App.css';
import Web3 from 'web3'
import {NODE_URL} from './../config/super';
import api from './../config/api';
class Tree extends React.Component {
    
    constructor(){
        super();
        this.url=api.tree_url;
        this.surl=api.stree_url;
        this.state={
            account:null,
            is_connected:false,
            tree:0,
        }
    }

    async componentDidMount(){
        if(window.ethereum){
            const web3 = new Web3(window.ethereum || NODE_URL);
            let accounts=await web3.eth.getAccounts();
            if(accounts.length!=="0"){
                this.setState({is_connected:true})
                this.setState({account:accounts[0]}) 
            }
        }
        else{
            
        }
    }

    render() {
        return (
            <div className="">
                <div id="main-wrapper">
                    <Header/>
                    <div className="content-body">
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-12">
                                    <div className="card border-0 pb-0 align-items-center p-3">
                                        <div>
                                        {
                                            this.state.tree===0?
                                                <button type="button" className="btn btn-success mx-2 btn-sm pending-button mb-1"  onClick={()=>this.setState({tree:0})}>{strings.ido}</button>
                                            :
                                                <button type="button" className="btn btn-warning mx-2 btn-sm pending-button mb-1"  onClick={()=>this.setState({tree:0})}>{strings.ido}</button>
                                        }
                                        {
                                            this.state.tree===1?
                                                <button type="button" className="btn btn-success mx-2 btn-sm pending-button mb-1"  onClick={()=>this.setState({tree:1})}>{"Stacking"}</button>
                                            :
                                            <button type="button" className="btn btn-warning mx-2 btn-sm pending-button mb-1"  onClick={()=>this.setState({tree:1})}>{"Stacking"}</button>
                                        }
                                        </div>
                                        
                                    </div>
                                </div>
                                {
                                    this.state.account!=null && this.state.tree===0?
                                        <iframe className="iframe" src={this.url+this.state.account} title="tree" height="800px" width="100%"></iframe> 
                                    :
                                        null
                                }

                                {
                                    this.state.account!=null && this.state.tree===1?
                                        <iframe className="iframe" src={this.surl+this.state.account} title="tree" height="800px" width="100%"></iframe> 
                                    :
                                        null
                                }
                                
                            </div>
                        </div>
                    </div>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default Tree;