import React from "react";
import Footer from '../../components/Footer';
import Header from "../../components/OwnerHeader";
import Web3 from 'web3'
import strings from "../../components/Language";
import './../../App.css';
import { toast } from 'react-toastify';
import {CONTRACT_ADDRESS,ABI,NODE_URL} from './../../config/stacking';
class Home extends React.Component {
    constructor(props) {
        super(props);
		this.state = {
            is_connected:false,
			lang:strings.getLanguage(),
			account:null,
            slot:0,
            onpress:0,
            stacking:0,
		}
    }

    async componentDidMount () {
		if(window.ethereum){
			const web3 = new Web3(window.ethereum || NODE_URL);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);

            let accounts=await web3.eth.getAccounts();
            if(accounts.length){
                this.setState({account:accounts[0]})
                this.setState({is_connected:true})
            }
            const slot = await SuperNode.methods.countslot().call();
            console.log(slot);
            if(slot){
                this.setState({slot:slot})
            }   

            const status = await SuperNode.methods._stackingStatus().call();
            if(status){
                this.setState({stacking:status});
            }
		}
	}

    async doROIPayout(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                SuperNode.methods.roiPayout().send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    that.setState({onpress:0})
                    resolve(true);
                    setTimeout(function(){
                        window.location.reload();
                    },5000)
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    that.alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.staked,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            this.alertMessage(strings.please_connect);
            this.setState({onpress:0})
        }
    }

    async doProfitPayout(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                SuperNode.methods.profitPayout(this.state.ceta,this.state.cetb).send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    that.setState({onpress:0})
                    resolve(true);
                    setTimeout(function(){
                        window.location.reload();
                    },5000)
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    that.alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.staked,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            this.alertMessage(strings.please_connect);
            this.setState({onpress:0})
        }
    }

    async doResetDailyCounter(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                SuperNode.methods.reset().send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    that.setState({onpress:0})
                    resolve(true);
                    setTimeout(function(){
                        window.location.reload();
                    },5000)
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    that.alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.staked,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            this.alertMessage(strings.please_connect);
            this.setState({onpress:0})
        }
    }

    async doEnableStacking(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                SuperNode.methods._stackingPurchaseEnable().send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    that.setState({onpress:0})
                    resolve(true);
                    setTimeout(function(){
                        window.location.reload();
                    },5000)
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    that.alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.staked,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            this.alertMessage(strings.please_connect);
            this.setState({onpress:0})
        }
    }

    async doDisableStacking(){
        if(this.state.is_connected){
            this.setState({onpress:1})
            let that=this;
            const web3 = new Web3(window.ethereum);
            const SuperNode = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
            const resolveAfter3Sec = new Promise((resolve, reject) =>{
                SuperNode.methods._stackingPurchaseDisable().send(
                    {
                        from: this.state.account,
                    })
                .on('transactionHash', function(hash){
                })
                .on('receipt', function(receipt){
                    that.setState({onpress:0})
                    resolve(true);
                    setTimeout(function(){
                        window.location.reload();
                    },5000)
                })
                .on('confirmation', function(confirmationNumber, receipt){
                })
                .on('error', function(error, receipt) {
                    that.alertMessage(error.message);
                    that.setState({onpress:0})
                    reject(error.message)
                });
            });
            toast.promise(
                resolveAfter3Sec,
                {
                    pending: strings.processing,
                    success: strings.staked,
                    error: strings.rejected,
                },{
                    position: "top-center",
                    autoClose: !1,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: 0,
                }
            )
        }
        else{
            this.alertMessage(strings.please_connect);
            this.setState({onpress:0})
        }
    }

    alertMessage(message){
        toast.error(message, {
            position: "top-center",
            autoClose: !1,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: 0,
        });
    }

    render() {
		return (
			<div className="">
				<div id="main-wrapper">
					<Header {...this.props}/>
					<div className="content-body">
						<div className="container-fluid">
							{this.state.account?
							<div className="alert alert-secondary text-white" role="alert">
								{strings.connected_wallet} : {this.state.account}
							</div>:null
							}
							<div className="row mb-4">
								<div className="col-lg-12">
									
								</div>
							</div>	
							<div className="row">
								<div className="col-xl-12 col-xxl-12 col-lg-12 col-sm-12">
                                    <div className="row">
										<div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="../images/card/card1-1.jpg" alt="CETA"/>
												<div className="card-info ">
                                                    <p className="mb-1 text-white fs-14">Total Stacking Lots</p>
                                                    <div className="d-block justify-content-between mb-4">
                                                        <h2 className="num-text text-white font-w600">{this.state.slot}</h2>
                                                    </div>
                                                    <div className="d-flex">
                                                        <div className="mr-4 mt-4 text-white">
                                                            
                                                        </div>
                                                    </div>
												</div>
											</div>
										</div>
										<div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="../images/card/card1-1.jpg" alt="CETA"/>
												<div className="card-info ">
                                                    <p className="mb-1 text-white fs-14">Payout</p>
                                                    <div className="d-block justify-content-between mb-4">
                                                    </div>
                                                    <div className="d-flex">
                                                        <div className="mr-1 mt-4 text-white">
                                                            {
                                                                this.state.onpress?
                                                                    <button type="button" className="btn btn-primary">
                                                                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                                        <span className="visually-hidden">&nbsp;Loading...</span>
                                                                    </button>
                                                                :
                                                                    <button type="button" className="btn btn-secondary" onClick={()=>this.doROIPayout()}>ROI Payout</button>
                                                            }
                                                        </div>
                                                        <div className="mr-1 mt-4 text-white">
                                                            <button type="button" className="btn btn-secondary" data-toggle="modal" data-target="#payout_modal">Profit Payout</button>
                                                        </div>
                                                    </div>
												</div>
											</div>
										</div>

                                        <div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="../images/card/card1-1.jpg" alt="CETA"/>
												<div className="card-info ">
                                                    <p className="mb-1 text-white fs-14">Reset Leader Board</p>
                                                    <div className="d-block justify-content-between mb-4">
                                                    </div>
                                                    <div className="d-flex">
                                                        <div className="mr-1 mt-4 text-white">
                                                            {
                                                                this.state.onpress?
                                                                    <button type="button" className="btn btn-primary">
                                                                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                                        <span className="visually-hidden">&nbsp;Loading...</span>
                                                                    </button>
                                                                :
                                                                    <button type="button" className="btn btn-secondary" onClick={()=>this.doResetDailyCounter()}>Reset</button>
                                                            }
                                                        </div>
                                                    </div>
												</div>
											</div>
										</div>

                                        <div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="../images/card/card1-1.jpg" alt="CETA"/>
												<div className="card-info ">
                                                    <p className="mb-1 text-white fs-14">Stacking Status</p>
                                                    <div className="d-block justify-content-between mb-4">
                                                        <h2 className="num-text text-white font-w600">{this.state.stacking?"Enabled":"Disabled"}</h2>
                                                    </div>
                                                    <div className="d-flex">
                                                        <div className="mr-1 mt-4 text-white">
                                                            {
                                                                this.state.onpress?
                                                                        <button type="button" className="btn btn-primary">
                                                                            <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                                            <span className="visually-hidden">&nbsp;Loading...</span>
                                                                        </button>:
                                                                        this.state.stacking?
                                                                            <button type="button" className="btn btn-secondary" onClick={()=>this.doDisableStacking()}>Disable</button>:
                                                                        <button type="button" className="btn btn-secondary" onClick={()=>this.doEnableStacking()}>Enable</button> 
                                                            }
                                                        </div>
                                                       
                                                    </div>
												</div>
											</div>
										</div>

									</div>	
								</div>
							</div>
						</div>
					</div>

                    <div className="modal fade" id="payout_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog modal-dialog-centered">
                            <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="staticBackdropLabel">Profit Payout</h5>
                            </div>
                            <div className="modal-body">
                                <div className="form-group">
                                    <label htmlFor="recipient-name" className="col-form-label">Amount of CETA:</label>
                                    <input type="text" className="form-control" id="recipient-name" onChange={(e)=>this.setState({ceta:e.target.value})}/>
                                </div>
                           
                                <div className="form-group">
                                    <label htmlFor="recipient-name" className="col-form-label">Amount of CETB:</label>
                                    <input type="text" className="form-control" id="recipient-name" onChange={(e)=>this.setState({cetb:e.target.value})}/>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">{strings.close}</button>
                                {
                                    this.state.onpress?
                                    <button type="button" className="btn btn-primary">
                                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                        <span className="visually-hidden">&nbsp;Loading...</span>
                                    </button>:
                                    <button type="button" className="btn btn-primary" onClick={()=>this.doProfitPayout()}>{strings.confirm}</button>
                                }
                                
                            </div>
                            </div>
                        </div>
                    </div>
					<Footer/>
				</div>
			</div>
		);
	}
}
export default Home;
