import React from "react";
import Footer from '../../components/Footer';
import Header from "../../components/OwnerHeader";
import Web3 from 'web3'
import strings from "../../components/Language";
import './../../App.css';
import axios from 'axios';
import api from './../../config/api';
import {getPlanName,getStatusAmount} from '../../components/Util';

class Report extends React.Component {
    constructor(props) {
        super(props);
		this.state = {
			lang:strings.getLanguage(),
			account:null,
			_my_stake:[],
		}
    }

    async componentDidMount () {
		if(window.ethereum){
			const web3 = new Web3(window.ethereum);
			var accounts = await web3.eth.getAccounts();
			if(accounts){
				this.setState({account:accounts[0]})
			}
			this.getReport();
		}
	}

	async getReport(){
        let self=this;
        await axios({
            method: 'post', url: api.stackList,
            data: {
               
            },
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(function (ress){
           console.log(ress.data)
		   self.setState({_my_stake:ress.data.stack.data});
        }).catch(function (e) {
            console.log(e)
        });
    }

    render() {
		return (
			<div className="">
				<div id="main-wrapper">
					<Header {...this.props}/>
					<div className="content-body">
						<div className="container-fluid">
							{this.state.account?
							<div className="alert alert-secondary text-white" role="alert">
								{strings.connected_wallet} : {this.state.account}
							</div>:null
							}
							<div className="row mb-4">
								<div className="col-lg-12">
									
								</div>
							</div>	
							<div className="row">
								<div className="col-xl-12 col-xxl-12 col-lg-12 col-sm-12">
									<div className="row">
										<div className="card border-0 pb-0" style={{width:'100%'}}>
											<table className="table">
												<thead>
													<tr>
														<th scope="col">Wallet</th>
														<th scope="col">{strings.plan}</th>
														<th scope="col">{strings.days}</th>
														<th scope="col">{strings.amount}</th>
														<th scope="col">{strings.set}</th>
														<th scope="col">{"Date"}</th>
													</tr>
												</thead>
												<tbody>
													{

														this.state._my_stake.map(function(data,index){
															return(
																<tr key={index}>
																	<th scope="row" className="text-white">{data.wallet}</th>
																	<td className="text-white">{getPlanName(data.plan)}</td>
																	<td className="text-white">{data.terms}</td>
																	<td className="text-white">{getStatusAmount(data.plan)}</td>
																	<td className="text-white">{data.set}</td>
																	<td className="text-white">{data.created_at}</td>
																</tr>
															);
														})
													}
													
												</tbody>
											</table>
										</div>
									</div>	
								</div>
							</div>
						</div>
					</div>
					<Footer/>
				</div>
			</div>
		);
	}
}
export default Report;