import React from "react";
import './../App.css';
import strings from "./Language";
import axios from 'axios';
import api from './../config/api';

class StackingLeader extends React.Component {
    constructor(){
        super();
        this.state={
            data:[],
        }
    }

    componentDidMount () {
       this.getLeaders();
    }

    getLeaders(){
        let self=this;
        axios({
            method: 'get', url: api.leaders,
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(function (ress){
           console.log(ress.data)
           self.setState({data:ress.data.leaders})
        }).catch(function (e) {
            console.log(e)
        });
    }

    render() {
        return (
            <div className="col-xl-4 col-xxl-8 col-lg-6">
                <div className="card staking-noti-bg">
                    <div className="card-header  border-0 pb-0">
                        <h4 className="text-white">{strings.top_leaders}</h4>
                    </div>
                    <div className="card-body"> 
                        <div id="DZ_W_Todo1" className="widget-media dz-scroll height500">
                            <ul className="timeline">
                                {
                                    this.state.data && this.state.data.length>0?this.state.data.map(function(d,i){
                                        return(
                                            <li key={i}>
                                                <div className="timeline-panel">
                                                    <div className="media mr-2">
                                                        <img alt="CETA" width="90" src={"../images/no"+(i+1)+".png"}/>
                                                    </div>
                                                    <div className="media-body">
                                                        <h5 className="mb-1">{d.wallet}</h5>
                                                        <small className="text-white d-block">{d.daily_child}</small>
                                                    </div>
                                                    
                                                </div>
                                            </li>
                                        );
                                    }):null
                                } 
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default StackingLeader;