import React from "react";
import './../App.css';
import Web3 from 'web3'
import detectEthereumProvider from '@metamask/detect-provider';
import { toast } from 'react-toastify';
import strings from "../components/Language";
import { NavLink } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import {getNetworkName,Network} from './../components/Util'
class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            is_connected:false,
            account:null,
            sponsor:"",
            onpress:0,
            net:"",
        }
    }

    async componentDidMount(){
        if(window.ethereum){
            const web3 = new Web3(window.ethereum);
            let accounts=await web3.eth.getAccounts();
            if(accounts.length===0){
                this.setState({is_connected:false})
            }
            else{
                this.setState({is_connected:true})
                this.setState({account:accounts[0]})
                this.setState({net:getNetworkName(window.ethereum.networkVersion)});
            }
            const networkId = await web3.eth.net.getId()
            if (networkId != 56) {
                await window.ethereum.request({
                    method: "wallet_addEthereumChain",
                    params: [
                        {
                            ...Network["bsc"]
                        }
                    ]
                });

                setTimeout(function(){
                    window.location.reload();
                },3000)
                return
            }
        }

        const provider = await detectEthereumProvider();
        if (provider) {
            window.ethereum.on("accountsChanged", async accounts => {
                if (accounts.length > 0){
                    let { web3 } = window
                    web3 = new Web3(web3.currentProvider);
                    let accounts=await web3.eth.getAccounts();
                    this.setState({account:accounts[0]})
                    setTimeout(function(){
                        window.location.reload();
                    },5000)
                }
                else{
                    this.setState({is_connected:false})
                }  
            });
        }
        else{
            this.alertMessage(strings.install_metamask);
        } 
    }

    async doConnect(){
        const provider = await detectEthereumProvider();
        if (provider) {
            try {
                await window.ethereum.request({ method: 'eth_requestAccounts' });
                this.setState({is_connected:true})
            } catch (error) {
                if (error.code === 4001) {
                    this.alertMessage(error.message);
                }
                else{
                    this.alertMessage(strings.install_metamask);
                }
              }
        } else {
            this.alertMessage(strings.install_metamask);
        }
    }

  /**
   * Change Language
   * @param {string} obj 
   */
   async changeLanguage(obj){
        if (obj === "zh") {
            strings.setLanguage('zh');
            await localStorage.setItem("language", 'zh');
            this.setState({ lang: strings.zh });
            window.location.reload();
        }
        else {
            strings.setLanguage('en');
            await localStorage.setItem("language", 'en');
            this.setState({ lang: strings.en });
            window.location.reload();
        }
    }

    alertMessage(message){
        toast.error(message, {
            position: "top-center",
            autoClose: !1,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: true,
            progress: 0,
        });
    }

    render() {
        let { location } = this.props
        let { pathname } = location;
        let splitLocation = pathname.split("/");
        return (
            <>
                <div className="nav-header">
                    <a href="/" className="brand-logo">
                        <img src="images/web-logo.png" className="logo-abbr mx-auto mt-3" width="65" height="65" alt="CETA"/>
                    </a>
                    <span className="nav-control" onClick={()=>{document.getElementById('main-wrapper').classList.toggle("menu-toggle")}}>
                        <div className="hamburger">
                            <span className="line"></span><span className="line"></span><span className="line"></span>
                        </div>
                    </span>
                </div>
                <div className="header">
                    <div className="header-content float-right">
                        <nav className="navbar navbar-expand">
                            <div className="collapse navbar-collapse justify-content-between">
                                <ul className="navbar-nav header-right main-notification ml-1">
                                    <li className="nav-item">
                                        <button className="badge badge-info d-block ml-1" data-toggle="modal" data-target="#language_modal">
                                        <i className="flaticon-047-earth-grid"></i></button>
                                    </li>
                                    <li className="nav-item">
                                        {
                                            this.state.is_connected?
                                            <button className="badge badge-success d-block ml-1">{this.state.net}</button>:
                                            <button className="badge badge-danger d-block ml-1" onClick={()=>this.doConnect()}>{strings.connect} </button>
                                            
                                        }
                                    </li>
                                    <li className="nav-item dropdown notification_dropdown">
                                        <a className="nav-link  ai-icon" href="void(0)" role="button" data-toggle="dropdown">
                                        <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <path d="M22.75 15.8385V13.0463C22.7471 10.8855 21.9385 8.80353 20.4821 7.20735C19.0258 5.61116 17.0264 4.61555 14.875 4.41516V2.625C14.875 2.39294 14.7828 2.17038 14.6187 2.00628C14.4546 1.84219 14.2321 1.75 14 1.75C13.7679 1.75 13.5454 1.84219 13.3813 2.00628C13.2172 2.17038 13.125 2.39294 13.125 2.625V4.41534C10.9736 4.61572 8.97429 5.61131 7.51794 7.20746C6.06159 8.80361 5.25291 10.8855 5.25 13.0463V15.8383C4.26257 16.0412 3.37529 16.5784 2.73774 17.3593C2.10019 18.1401 1.75134 19.1169 1.75 20.125C1.75076 20.821 2.02757 21.4882 2.51969 21.9803C3.01181 22.4724 3.67904 22.7492 4.375 22.75H9.71346C9.91521 23.738 10.452 24.6259 11.2331 25.2636C12.0142 25.9013 12.9916 26.2497 14 26.2497C15.0084 26.2497 15.9858 25.9013 16.7669 25.2636C17.548 24.6259 18.0848 23.738 18.2865 22.75H23.625C24.321 22.7492 24.9882 22.4724 25.4803 21.9803C25.9724 21.4882 26.2492 20.821 26.25 20.125C26.2486 19.117 25.8998 18.1402 25.2622 17.3594C24.6247 16.5786 23.7374 16.0414 22.75 15.8385ZM7 13.0463C7.00232 11.2113 7.73226 9.45223 9.02974 8.15474C10.3272 6.85726 12.0863 6.12732 13.9212 6.125H14.0788C15.9137 6.12732 17.6728 6.85726 18.9703 8.15474C20.2677 9.45223 20.9977 11.2113 21 13.0463V15.75H7V13.0463ZM14 24.5C13.4589 24.4983 12.9316 24.3292 12.4905 24.0159C12.0493 23.7026 11.716 23.2604 11.5363 22.75H16.4637C16.284 23.2604 15.9507 23.7026 15.5095 24.0159C15.0684 24.3292 14.5411 24.4983 14 24.5ZM23.625 21H4.375C4.14298 20.9999 3.9205 20.9076 3.75644 20.7436C3.59237 20.5795 3.50014 20.357 3.5 20.125C3.50076 19.429 3.77757 18.7618 4.26969 18.2697C4.76181 17.7776 5.42904 17.5008 6.125 17.5H21.875C22.571 17.5008 23.2382 17.7776 23.7303 18.2697C24.2224 18.7618 24.4992 19.429 24.5 20.125C24.4999 20.357 24.4076 20.5795 24.2436 20.7436C24.0795 20.9076 23.857 20.9999 23.625 21Z" fill="#EB8153"/>
                                            </svg>
                                        </a>
                                        <div className="dropdown-menu dropdown-menu-right">
                                            <div id="dlab_W_Notification1" className="widget-media dz-scroll p-3 height380">
                                                <ul className="timeline">
                                                   
                                                </ul>
                                            </div>
                                            <a className="all-notification" href="/">{strings.all_notification} <i className="ti-arrow-right"></i></a>
                                        </div>
                                    </li>
                                </ul>	 
                            </div>
                        </nav>
                    </div>
                </div>
                <div className="deznav">
                    <div className="deznav-scroll">
                        <ul className="metismenu" id="menu">
                            <li className={splitLocation[1] === "" ? "mm-active" : ""}>
                                <NavLink exact activeClassName="mm-active" to="/">
                                    <i className="flaticon-144-layout"></i>
                                    <span className="nav-text">{strings.dashboard}</span>
                                </NavLink>
                            </li>
                            {/* <li className={splitLocation[1] === "genealogy" ? "mm-active" : ""}>
                                <NavLink activeClassName="mm-active" to="/genealogy">
                                    <i className="flaticon-381-windows"></i>
                                    <span className="nav-text">{strings.genealogy}</span>
                                </NavLink>
                            </li> */}
                            <li className={splitLocation[1] === "ido" ? "mm-active" : ""}>
                                <NavLink activeClassName="mm-active" to="/ido">
                                    <i className="flaticon-381-notebook"></i>
                                    <span className="nav-text">{strings.ido}</span>
                                </NavLink>
                            </li>
                            <li className={splitLocation[1] === "staking" ? "mm-active" : ""}>
                                <NavLink activeClassName="mm-active" to="/staking">
                                    <i className="flaticon-381-network"></i>
                                    <span className="nav-text">{strings.staking}</span>
                                </NavLink>
                            </li>
                            {/* <li>
                                <a href="/trade">
                                    <i className="flaticon-381-repeat-1"></i>
                                    <span className="nav-text">{strings.trade}</span>
                                    <i className="flaticon-168-padlock float-right text-red"></i>
                                </a>
                            </li>
 							<li className={splitLocation[1] === "luckybox" ? "mm-active" : ""}>
                                <NavLink activeClassName="mm-active" to="/luckybox">
                                    <i className="flaticon-381-box-1"></i>
                                    <span className="nav-text">{strings.luckybox}</span>
                                </NavLink>
                            </li>
                            <li className={splitLocation[1] === "mystery" ? "mm-active" : ""}>
                                <NavLink activeClassName="mm-active" to="/mystery">
                                    <i className="flaticon-381-bookmark"></i>
                                    <span className="nav-text">{strings.spin_wheel}</span>
                                </NavLink>
                            </li>
                            <li className={splitLocation[1] === "prediction" ? "mm-active" : ""}>
                                <NavLink activeClassName="mm-active" to="/prediction">
                                    <i className="flaticon-381-view-1"></i>
                                    <span className="nav-text">{strings.prediction}</span>
                                </NavLink>
                            </li> */}
                            {/*<li>
                                <a href="/prediction">
                                    <i className="flaticon-381-view-1"></i>
                                    <span className="nav-text">{strings.prediction}</span>
                                    <i className="flaticon-168-padlock float-right text-red"></i>
                                </a>
                                <a href="prediction" className="has-arrow ai-icon" aria-expanded="false">
                                    <i className="flaticon-381-view-1"></i>
                                    <span className="nav-text">Prediction</span>
                                    <i className="flaticon-168-padlock float-right text-red"></i>
                                </a>
                                <ul aria-expanded="false">
                                    <li><a href="prediction">Crypto Live</a></li>
                                    <li><a href="sports">Sports</a></li>
                                    <li><a href="esports">ESports</a></li>
                                </ul>
                            </li>*/}
                            {/* <li><a href="https://cetatv.cryptoempire.live" target="_blank" rel="noreferrer">
                                    <i className="flaticon-381-television"></i>
                                    <span className="nav-text">{strings.movie}</span>
                                </a>
                            </li> */}
                            {/* <li>
                                <a href="/game">
                                    <i className="flaticon-061-puzzle"></i>
                                    <span className="nav-text">{strings.game}</span>
                                    <i className="flaticon-168-padlock float-right text-red"></i>
                                </a>
                            </li> */}
                        </ul>
                    </div>
                </div>

                <div className="modal fade" id="language_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="staticBackdropLabel">{strings.language}</h5>
                        </div>
                        <div className="modal-body">
                            <div className="form-group">
                                <button type="button" className="btn text-white" data-dismiss="modal" onClick={()=>{this.changeLanguage('zh')}}> <img src="images/zh.png" className="head-mobile-img lang-icon" alt="CETA"/> </button>
                            </div>
                            <div className="form-group">
                                <button type="button" className="btn text-white" data-dismiss="modal" onClick={()=>{this.changeLanguage('en')}}> <img src="images/en.png" className="head-mobile-img lang-icon" alt="CETA"/> English</button>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">{strings.close}</button>
                        </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default withRouter(Header);