import React from "react";
import Footer from '../components/Footer';
import Header from "../components/Header";
import Web3 from 'web3'
import strings from "../components/Language";
import './../App.css';
import axios from 'axios';
import api from './../config/api';
import {alertMessage} from '../components/Util';

class Report extends React.Component {
    constructor(props) {
        super(props);
		this.state = {
			lang:strings.getLanguage(),
			account:null,
			report_data: [],
			next_page:null,
			previous_page:null,
			auth_token:"",
			is_connected:false,
		}
		this.signPopup = React.createRef();
    }

    async componentDidMount () {
		if(window.ethereum){
			this.setState({auth_token:localStorage.getItem('auth_token')});
			const web3 = new Web3(window.ethereum);
			var accounts = await web3.eth.getAccounts();
			if(accounts){
				this.setState({account:accounts[0]});
				this.setState({is_connected:true})
                this.getReport();
			}
			if(!this.state.auth_token){
				this.signPopup.current.click();
			}
		}
	}

	async getReport(){
        let self=this;
        await axios({
            method: 'get', url: api.cetaReport,
            data: {
               
            },
            headers: {
                'Content-Type': 'application/json',
				'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){
           console.log(ress.data)
		   self.setState({
			    report_data:ress.data.data.data,
				previous_page:ress.data.data.prev_page_url,
				next_page:ress.data.data.next_page_url,
			});
        }).catch(function (e) {
            if(e.response.status===401){
                self.signPopup.current.click();
            }
        });
    }

	async getPageReport(url){
		if(url==null){
			return 0;
		}
        let self=this;
        await axios({
            method: 'get', url: url+"&wallet="+this.state.account,
            data: {
               
            },
            headers: {
				'Content-Type': 'application/json',
				'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){
		   self.setState({
				report_data:ress.data.data.data,
				previous_page:ress.data.data.prev_page_url,
				next_page:ress.data.data.next_page_url,
			});
        }).catch(function (e) {
            if(e.response.status===401){
                self.signPopup.current.click();
            }
        });
    }

	 /**
     * Sign Message for SingIn
     */
	  async doSign(){
        let that=this;
        if(this.state.is_connected){
            const web3 = new Web3(window.ethereum);
            const string = "THE_CETA_PROJECT";
            const accounts = await web3.eth.getAccounts();
            const signature = new Promise((resolve, reject) =>{
                web3.eth.personal.sign(string, accounts[0], "test").then(function(data){
                    console.log(data);
                    that.login(accounts[0],data);
                    resolve(true);
                });
            });
            console.log(signature)
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    /**
     * Login
     */
     async login(wallet,secret){
        await axios({
            method: 'post', url: api.register,
            data: {
                wallet:wallet,
                secret:secret,
                device_name:"CETA"
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
            }
        }).then(function (ress){
            localStorage.setItem("auth_token",ress.data);
            setTimeout(function(){
                window.location.reload();
            },2000)
        }).catch(function (e) {
            console.log(e)
        });
    }

    render() {
		return (
			<div className="">
				<div id="main-wrapper">
					<Header {...this.props}/>
					<div className="content-body">
						<div className="container-fluid">
							<div className="row mb-4">
								<div className="col-lg-12">
									
								</div>
							</div>	
							<div className="row">
								<div className="col-xl-12 col-xxl-12 col-lg-12 col-sm-12">
									<div className="row">
										<div className="card border-0 pb-0" style={{width:'100%'}}>
											<table className="table">
												<thead>
													<tr>
														<th scope="col">{strings.amount}</th>
														<th scope="col">{strings.description}</th>
														<th scope="col">{strings.date}</th>
													</tr>
												</thead>
												<tbody>
													{

														this.state.report_data.map(function(data,index){
															return(
																<tr key={index}>
																	<th scope="row" className="text-white">{data.amount/1000000000}</th>
																	<td className="text-white">{data._di}</td>
																	<td className="text-white">{data.created_at}</td>
																</tr>
															);
														})
													}
												</tbody>
												<tfoot>
													<tr className="text-center">
														<th colSpan={3}>
															{
																this.state.previous_page?
																	<button type="button" className="btn btn-info mx-2 btn-sm" onClick={()=>this.getPageReport(this.state.previous_page)}>{strings.previous}</button>:
																	<button type="button" className="btn btn-secondary mx-2 btn-sm" >{strings.previous}</button>
															}
															{
																this.state.next_page?
																	<button type="button" className="btn btn-info mx-2 btn-sm" onClick={()=>this.getPageReport(this.state.next_page)}>{strings.next}</button>:
																	<button type="button" className="btn btn-secondary mx-2 btn-sm">{strings.next}</button>
															}
														</th>
													</tr>
												</tfoot>
											</table>
										</div>
									</div>	
								</div>
							</div>
						</div>
					</div>
					<Footer/>
				</div>
				<button ref={this.signPopup} type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#sign_modal">Small modal</button>
                <div className="modal fade" id="sign_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="staticBackdropLabel">{strings.signin_to_continue}</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body text-center">
                            <h6>{this.state.account}</h6>
                                <button type="button" className="btn btn-success" onClick={()=>this.doSign()}>{strings.signin}</button>
                            </div>
                        </div>
                    </div>
                </div>
			</div>
		);
	}
}
export default Report;