import React from "react";
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import IDO from "./pages/ido";
import Home from './pages/index';
import Trade from './pages/trade';
import Stacking from './pages/stacking';
import Tree from './pages/tree';
import CETAReport from './pages/cetareport';
import CETBReport from './pages/cetbreport';
import Prediction from './pages/prediction';
import LuckyBox from './pages/games/luckybox';
import MistryBox from './pages/games/mistrybox';
import OwnerHome from './pages/owner';
import Report from "./pages/owner/report";
import LeaderBoard from "./pages/owner/leaders";

class App extends React.Component {
  render(){
    return (
      <>
      <BrowserRouter basename="/">
        <ToastContainer/>
        <Switch>
          <Route exact  path="/" component={Home}/>
          {/* <Route path="/trade" component={Trade}/> */}
          <Route path="/ido" component={IDO}/>
          <Route path="/staking" component={Stacking}/>
          {/* <Route path="/prediction" component={Prediction}/>
		  <Route path="/luckybox" component={LuckyBox}/>
          <Route path="/mystery" component={MistryBox}/> */}
          {/* <Route path="/genealogy" component={Tree}/> */}
          {/* <Route path="/ceta" component={CETAReport}/>
          <Route path="/cetb" component={CETBReport}/> */}

          {/*Owner Route*/}
          <Route path="/owner/index" component={OwnerHome}/>
          <Route path="/owner/report" component={Report}/>
          <Route path="/owner/leaderboard" component={LeaderBoard}/>
        </Switch>
      </BrowserRouter>
      </>
    );
  }
}
export default App;
