import React from "react";
import Footer from '../../components/Footer';
import Header from "../../components/OwnerHeader";
import StackingLeader from "../../components/StackingLeader";
import Web3 from 'web3'
import strings from "../../components/Language";
import './../../App.css';
class LeaderBoard extends React.Component {
    constructor(props) {
        super(props);
		this.state = {
			lang:strings.getLanguage(),
			account:null,
		}
    }

    async componentDidMount () {
		if(window.ethereum){
			const web3 = new Web3(window.ethereum);
			var accounts = await web3.eth.getAccounts();
			if(accounts){
				this.setState({account:accounts[0]})
			}
		}
	}

    render() {
		return (
			<div className="">
				<div id="main-wrapper">
					<Header {...this.props}/>
					<div className="content-body">
						<div className="container-fluid">
							{this.state.account?
							<div className="alert alert-secondary text-white" role="alert">
								{strings.connected_wallet} : {this.state.account}
							</div>:null
							}
							<div className="row mb-4">
								<div className="col-lg-12">
									
								</div>
							</div>	
							<div className="row">
								<StackingLeader {...this.props}/>
								<div className="col-xl-8 col-xxl-8 col-lg-12 col-sm-12">
									<div className="row">
									</div>	
								</div>
							</div>
						</div>
					</div>
					<Footer/>
				</div>
			</div>
		);
	}
}
export default LeaderBoard;
