export function getStackingPrice(plan,set){
    plan=parseInt(plan);
    if(plan===1){
        return [90*set,210*set];
    }
    else if(plan===2){
        return [120*set,180*set];
    }
    else if(plan===3){
        return [150*set,150*set];
    }
    else if(plan===4){
        return [300*set,0];
    }
}

export function getPlanName(plan){
    plan=parseInt(plan);
    if(plan===1){
        return "30%-70%";
    }
    else if(plan===2){
        return "40%-60%";
    }
    else if(plan===3){
        return "50%-50%";
    }
    else if(plan===4){
        return "100%";
    }
}

export function getStatusName(plan){
    plan=parseInt(plan);
    if(plan===1){
        return "Ongoing";
    }
    else if(plan===2){
        return "Expired";
    }
    else if(plan===3){
        return "Collected";
    }
}

export function getStatusAmount(plan){
    plan=parseInt(plan);
    if(plan===1){
        return "CETA:90 USDT:210";
    }
    else if(plan===2){
        return "CETA:120 USDT:180";
    }
    else if(plan===3){
        return "CETA:150 USDT:150";
    }
    else if(plan===4){
        return "CETA:300";
    }
}

