import axios from "axios";
import React from "react";
import Web3 from "web3";
import Footer from '../components/Footer';
import Header from "../components/Header";
import api from "../config/api";
import './../App.css';
import {NODE_URL} from './../config/mistrybox';
import strings from "./../components/Language";
import {alertMessage,successMessage} from './../components/Util';
class Prediction extends React.Component {
    state = {
        Contract:null,
        account:null,
        is_connected:false,
        onpress:0,
        result:"",
        balance:0,
        price:0,
        amount:"",
        locked_price:0,
        type:1,//1=Up 2=Down
        bet_id:0,
        timer:"00:00",
        bet_list:[],
        billboard_list:[],
        my_rank:0,
        balance_start:false,
        lang:strings.getLanguage(),
    };

    constructor() {
        super();
        this.prizePopup = React.createRef();
        this.signPopup = React.createRef();
    }

    async componentDidMount(){
        new window.TradingView.widget(
            {
                "autosize": true,
                "symbol": "BINANCE:BNBUSDT",
                "interval": "5",
                "timezone": "Asia/Hong_Kong",
                "theme": "dark",
                "style": "1",
                "locale": "en",
                "toolbar_bg": "#f1f3f6",
                "enable_publishing": false,
                "hide_top_toolbar": true,
                "save_image": false,
                "container_id": "tradingview_5328c",
            }
        );
        this.setState({auth_token:localStorage.getItem('auth_token')});
        if(window.ethereum){
            const web3 = new Web3(window.ethereum || NODE_URL);
            let accounts=await web3.eth.getAccounts();
            if(accounts.length){
				this.setState({is_connected:true})
				this.setState({account:accounts[0]}) 
                this.getBalance();
                this.getBNBPrice();
                let that=this;
                if(this.state.auth_token){
                    setInterval(function () {
                        that.getBNBPrice();
                    }, 10000);
                }
                else{
                    this.signPopup.current.click();
                }
            }
        }
        else{
        }
    }

    /**
     * Get Balance
     */
      async getBalance(){
        let that=this;
        await axios({
            method: 'post', url: api.spin_wheel_balance,
            data: {
                wallet:this.state.account
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){  
           if(ress.data.error===200){
                that.setState({balance:ress.data.prediction})  
                if(that.state.balance_start==false){
                    that.setState({balance_start:true})
                    setInterval(function () {that.getBalance();}, 10000);
                }
           }
           else{
                that.signPopup.current.click();
           }
        }).catch(function (e) {
            if(e.response.status===401){
                that.signPopup.current.click();
            }
        });
    }

    async getBNBPrice(){
        let that=this;
        await axios({
            method: 'get', url: api.bnbusdt_price
        }).then(function (ress){
           that.setState({price:ress.data.price})
        }).catch(function (e) {
            console.log(e)
        });
    }

    async doPredict(){
        if(this.state.is_connected){
            let that=this;
            let price=this.state.price;
            this.setState({locked_price:price,onpress:1})
            await axios({
                method: 'post', url: api.prediction_bet,
                data: {
                    wallet:this.state.account,
                    price:price,
                    amount:this.state.amount,
                    type:this.state.type
                },
                headers: {
                    'Content-Type': 'application/json',
                    'Accept-Language' : this.state.lang,
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + this.state.auth_token,
                }
            }).then(function (ress){
                console.log(ress.data)
            if(ress.data.error===200){
                that.setState({bet_id:ress.data.bet_id});
                that.startPredict();
                that.getBalance();
                that.prizePopup.current.click();
            }
            else{
                alertMessage(ress.data.message);
                that.setState({onpress:0,locked_price:0})
            }
            }).catch(function (e) {
                that.setState({onpress:0,locked_price:0})
                if(e.response.status===401){
                    that.signPopup.current.click();
                }
            });
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    async startPredict(){
        let counter=300;
        let that=this;
        var handle = setInterval(function(){
            counter--;
            var minutes = Math.floor(counter / 60);
            var seconds = counter - minutes * 60;
            let timer=(minutes < 10 ? "0" + minutes : minutes)+":"+(seconds < 10 ? "0" + seconds : seconds)
            that.setState({timer:timer})
            if(counter==0){
                clearInterval(handle);
                that.getBet();
            }
        }, 1000);
    }

    async getBet(){
        if(this.state.is_connected){
            let that=this;
            let price=this.state.price;
            this.setState({locked_price:price})
            await axios({
                method: 'post', url: api.prediction_check,
                data: {
                    bet_id:this.state.bet_id,
                },
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Accept-Language' : this.state.lang,
                    'Authorization': 'Bearer ' + this.state.auth_token,
                }
            }).then(function (ress){
                if(ress.data.error===200){
                    that.getBalance();
                    if(ress.data.result){
                        successMessage(strings.you_win +" "+ress.data.amount);
                    }
                    else{
                        alertMessage("Sorry Better Luck Next Time");
                    }
                    that.setState({onpress:0,locked_price:0,amount:0});
                }
                else{
                    setTimeout(function(){
                        that.getBet()
                    },2000)
                }
            }).catch(function (e) {
                if(e.response.status===401){
                    that.signPopup.current.click();
                }
            });
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    /**
     * Get Play Bet List
     * @param {string} wallet 
     */
      async getBetList(wallet){
        let that=this;
        await axios({
            method: 'get', url: api.prediction_list+"?wallet="+this.state.account,
            data: {
                wallet: wallet
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){
           that.setState({bet_list:ress.data.bets.data});
        }).catch(function (e) {
            if(e.response.status===401){
                that.signPopup.current.click();
            }
        });
    }

    /**
     * Get Play Bet List
     * @param {string} wallet 
     */
     async getBillboard(wallet){
        let that=this;
        await axios({
            method: 'get', url: api.prediction_billboard,
            data: {
                wallet: wallet
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
                'Authorization': 'Bearer ' + this.state.auth_token,
            }
        }).then(function (ress){
            that.setState({billboard_list:ress.data.bets,my_rank:ress.data.my_rank});
        }).catch(function (e) {
            if(e.response.status===401){
                that.signPopup.current.click();
            }
        });
    }

    async doSign(){
        let that=this;
        if(this.state.is_connected){
            const web3 = new Web3(window.ethereum);
            const string = "THE_CETA_PROJECT";
            const accounts = await web3.eth.getAccounts();
            const signature = new Promise((resolve, reject) =>{
                web3.eth.personal.sign(string, accounts[0], "test").then(function(data){
                    console.log(data);
                    that.login(accounts[0],data);
                    resolve(true);
                });
            });
            console.log(signature)
        }
        else{
            alertMessage(strings.please_connect);
        }
    }

    /**
     * Login
     */
     async login(wallet,secret){
        await axios({
            method: 'post', url: api.register,
            data: {
                wallet:wallet,
                secret:secret,
                device_name:"CETA"
            },
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Accept-Language' : this.state.lang,
            }
        }).then(function (ress){
            localStorage.setItem("auth_token",ress.data);
            setTimeout(function(){
                window.location.reload();
            },2000)
        }).catch(function (e) {
            console.log(e)
        });
    }

    render(){
        let that=this;
        return (
            <div className="">
                <div id="main-wrapper">
                    <Header/>
                    <div className="content-body">
                        <div className="container-fluid">
                            <div className="row mb-5 text-center align-content-center ">
                                <div className="col-lg-6 mx-auto">
                                    <img src="images/ce-cryptolive-head.png" className="img-fluid"/>
                                </div>
                            </div>
                            <div className="text-center">
                                <h3>BNB/USDT : {this.state.price}</h3>
                            </div>
                            <div className="text-center">
                                <h5>{strings.available_balance} : {this.state.balance}</h5>
                                <span className="modal-title text-center text-warning">{strings.lock_price+" : "}{this.state.locked_price}</span>
                            </div>
                            <div className="text-center mt-1">
                                <button type="button" className="btn btn-danger mx-2 btn-sm" data-toggle="modal" data-target="#do_predict_modal" onClick={()=>this.setState({type:2})} do_predict_modal>{strings.prediction_up}</button>
                                <button type="button" className="btn btn-success mx-2 btn-sm" data-toggle="modal" data-target="#do_predict_modal" onClick={()=>this.setState({type:1})} >{strings.prediction_down}</button>
                            </div>
                            <div className="form-group timer-container mt-1">
                                <div className="timer-box">
                                    <span className="modal-title text-center text-info" style={{fontSize:25}}>{this.state.timer}</span>
                                </div>
                            </div>
                            <div className="text-center mt-1">
                                <a rel="noreferrer" href="#" data-toggle="modal" data-target="#history_modal" onClick={()=>this.getBetList()}><small className="badge badge-primary">{strings.view_history}</small></a>
                                &nbsp;&nbsp;<a rel="noreferrer" href="#" data-toggle="modal" data-target="#billboard_modal" onClick={()=>this.getBillboard()}><small className="badge badge-info">{strings.billboard}</small></a>
                            </div>
                            <div className="my-2">
                                <div className="tradingview-widget-container">
                                    <div id="tradingview_5328c" style={{height:450}}></div>
                                </div>
				            </div>
			            </div>
                    </div>
                    <Footer/>
                </div>

                <div className="modal fade" id="do_predict_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                            <div className="modal-header">
                                {
                                    this.state.type===1?
                                    <span className="modal-title text-center text-success">{strings.prediction_up}</span>
                                    :
                                    <span className="modal-title text-center text-danger">{strings.prediction_down}</span>
                                }
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" ref={this.prizePopup}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body text-center">
                                <span className="modal-title text-center text-warning">{"BNBUSDT : "}{this.state.price}</span><br/>
                                <span className="modal-title text-center text-warning">{strings.lock_price+" : "}{this.state.locked_price}</span>
                                <div className="text-center mt-3">
                                    <h5>{strings.available_balance}</h5>
                                </div>
                                <span className="modal-title text-center text-warning">{this.state.balance}</span><br/>
                                <div className="form-group mt-3">
                                    <input className="form-control" value={this.state.amount} onChange={(e)=>this.setState({amount:e.target.value})} placeholder={strings.enter_amount}/>
                                </div>
                                <div className="form-group text-center">
                                    {
                                        this.state.onpress ===1?
                                            <button type="button" className="btn btn-primary btn-sm">
                                                <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                                <span className="visually-hidden">&nbsp;{strings.calc}</span>
                                            </button>
                                        :
                                        <button type="button" className="btn btn-primary btn-sm" onClick={()=>this.doPredict()}>{strings.predict}</button>
                                    }
                                </div>
                            </div>
                           
                        </div>
                    </div>
                </div>
                <button ref={this.signPopup} type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#sign_modal">Small modal</button>
                <div className="modal fade" id="history_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <div className="col-12 text-center">
                                    <h4 className="text-center modal-title">{strings.history}</h4>
                                </div>
                                <button type="button" className="close" data-dismiss="modal"><span>&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <div className="col-12 luckyboxItem text-center mx-auto">
                                    <table className="table">
                                        <thead>
                                            <tr>
                                                <th scope="col">{strings.amount}</th>
                                                <th scope="col">{strings.prediction}</th>
                                                <th scope="col">{strings.lock_price}</th>
                                                <th scope="col">{strings.open_price}</th>
                                                <th scope="col">{strings.result}</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {

                                                this.state.bet_list.map(function(data,index){
                                                    return(
                                                        <tr key={index}>
                                                            <th scope="row" className="text-white">{data.amount}</th>
                                                            <td className="text-white">{data.type==1?strings.up:strings.down}</td>
                                                            <td className="text-white">{data.price}</td>
                                                            <td className="text-white">{data.cprice}</td>
                                                            <td className="text-white">{data.is_win==1?strings.win:strings.lose}</td>
                                                        </tr>
                                                    );
                                                })
                                            }
                                            
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="modal-footer mx-auto">
                                <button type="button" className="btn btn-danger light" data-dismiss="modal">{strings.close}</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="modal fade" id="billboard_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <div className="col-12 text-center">
                                    <h4 className="text-center modal-title">{strings.billboard}</h4>
                                </div>
                                <button type="button" className="close" data-dismiss="modal"><span>&times;</span></button>
                            </div>
                            <div className="modal-body">
                                <div className="col-12 text-center">
                                    <p className="badge badge-info">{strings.your_placing+" # : "+this.state.my_rank}</p>
                                </div>
                                <div className="col-12 luckyboxItem text-center mx-auto" style={{height:400,overflowX:"hidden",overflowY:"scroll"}}>
                                    <table className="table">
                                        <thead>
                                            <tr>
                                                <th scope="col">{"#"}</th>
                                                <th scope="col">{strings.wallet}</th>
                                                <th scope="col">{strings.point}</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {

                                                this.state.billboard_list.map(function(data,index){
                                                    return(
                                                        <tr key={index} className={that.state.account==data.wallet?"badge-success":""}>
                                                            <th scope="row" className="text-white">{index+1}</th>
                                                            <td className="text-white">{data.wallet.substring(0, 4)+"***"+data.wallet.substring(35)}</td>
                                                            <td className="text-white">{data.prediction}</td>
                                                        </tr>
                                                    );
                                                })
                                            }
                                            
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="modal fade" id="sign_modal" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="staticBackdropLabel">{strings.signin_to_continue}</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body text-center">
                            <h6>{this.state.account}</h6>
                                <button type="button" className="btn btn-success" onClick={()=>this.doSign()}>{strings.signin}</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Prediction;