import React from "react";
import Footer from '../components/Footer';
import Header from "../components/Header";
import StackingLeader from "../components/StackingLeader";
import Web3 from 'web3'
import strings from "../components/Language";
import './../App.css';
class Home extends React.Component {
    constructor(props) {
        super(props);
		this.state = {
			lang:strings.getLanguage(),
			account:null,
		}
    }

    async componentDidMount(){
		if(window.ethereum){
			const web3 = new Web3(window.ethereum);
			var accounts = await web3.eth.getAccounts();
			if(accounts){
				this.setState({account:accounts[0]})
			}
		}
	}

    render() {
		return (
			<div className="">
				<div id="main-wrapper">
					<Header {...this.props}/>
					<div className="content-body">
						<div className="container-fluid">
							{this.state.account?
							<div className="alert alert-secondary text-white" role="alert">
								{strings.connected_wallet} : {this.state.account}
							</div>:null
							}
							<div className="row mb-4">
								<div className="col-lg-12">
									<div className="bootstrap-carousel">
										<div data-ride="carousel" className="carousel slide" id="carouselExampleCaptions">
											<ol className="carousel-indicators zindex-class">
												<li className="active" data-slide-to="0" data-target="#carouselExampleCaptions"></li>
												<li data-slide-to="1" data-target="#carouselExampleCaptions" className=""></li>
												<li data-slide-to="2" data-target="#carouselExampleCaptions" className=""></li>
											</ol>
											<div className="carousel-inner">
												<div className="carousel-item active">
													<img className="d-block w-100" src="./images/home-banner1.jpg" alt="CETA"/>
													{/* <div className="carousel-caption d-none d-flex justify-content-center">
														<a href={this.state.lang==="zh"?"CE White paper V1.10 Chinese.pdf":"CE White paper V1.10.pdf"} rel="noreferrer" className="btn btn-rounded btn-danger d-block ml-1">{strings.whitepaper}</a>
													</div> */}
												</div>
											</div>
											<a data-slide="prev" href="#carouselExampleCaptions" className="carousel-control-prev"><span
													className="carousel-control-prev-icon"></span> <span
													className="sr-only">Previous</span>
											</a>
											<a data-slide="next" href="#carouselExampleCaptions" className="carousel-control-next"><span
													className="carousel-control-next-icon"></span> <span
													className="sr-only">Next</span>
											</a>
										</div>
									</div>
								</div>
							</div>	
							<div className="row">
								{/* <StackingLeader {...this.props}/> */}
								<div className="col-xl-8 col-xxl-8 col-lg-12 col-sm-12">
									<div className="row">
										<div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="images/card/card1-1.jpg" alt="CETA"/>
												
												<div className="card-info ">
													
														<p className="mb-1 text-white fs-14">{strings.start_harvesting}</p>
														<div className="d-block justify-content-between mb-4">
															<h2 className="num-text text-white font-w600">CET</h2>
															<p className="text-white">{strings.formatString(strings.we_accept,<br/>)}</p>
														</div>
														<div className="d-flex">
															<div className="mr-4 mt-4 text-white">
																<button type="button" className="btn btn-secondary">{strings.coming_soon}</button>
															</div>
														</div>
												</div>
											</div>
										</div>
										<div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="images/card/card2-1.jpg" alt="CETA"/>
												<div className="card-info">
													<p className="fs-14 mb-1 text-white">{strings.live_prediction}</p>
													<div className="d-block justify-content-between mb-4">
														<h2 className="num-text text-white font-w600">{strings.for_sport_esport}</h2>
														<p className="text-white">{strings.instant_payout}</p>
													</div>
													<div className="d-flex">
														<div className="mr-4 text-white">
															<button type="button" className="btn btn-warning">{strings.coming_soon}</button>
														</div>
													</div>
												</div>
											</div>
										</div>
										{/* <div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="images/card/card3-1.jpg" alt="CETA"/>
												<div className="card-info">
													<p className="mb-1 text-white fs-14">{strings.free_movei}</p>
													<div className="d-block justify-content-between mb-4">
														<h2 className="num-text text-white  font-w600">CETA TV</h2>
														<p className="text-white">{strings.live_stream}</p>
													</div>
													<div className="d-flex">
														<div className="mr-4 text-white">
															<a href="https://cetatv.cryptoempire.live" target="_blank" rel="noreferrer" type="button" className="btn btn-info">{strings.watch_now}</a>
														</div>
													</div>
												</div>
											</div>
										</div> */}
										<div className="col-sm-6 height320">
											<div className="card-bx stacked card">
												<img src="images/card/card4-1.jpg" alt="CETA"/>
												<div className="card-info">
													<p className="mb-1 text-white fs-14">{strings.join_now}</p>
													<div className="d-block justify-content-between mb-4">
														<h2 className="num-text text-white font-w600">{strings.ceta_gammunity}</h2>
														<p className="text-white">{strings.nft_win}</p>
													</div>
													<div className="d-flex">
														<div className="mr-4 text-white">
															<button type="button" className="btn btn-success">{strings.coming_soon}</button>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>	
								</div>
							</div>
						</div>
					</div>
					<Footer/>
				</div>
			</div>
		);
	}
}
export default Home;
